$color-primary: #004a7c;
$color-secondary: #005691;
$color-tertiary: #e8f1f5;
$color-white: #fafafa;
$color-grey: #E1E1E1;


.resume {
    background-color: $color-tertiary;
    padding: 1rem 5rem 1rem 5rem;

    &-container {
        display: flex;
        flex: 1;
        overflow-x: scroll;
        overflow-y: visible;
        display: grid;
        grid-template-columns: repeat(6, min-content);
        grid-template-rows: repeat(2, 1fr) 50px repeat(2, 1fr);
        justify-content: flex-start;
    }

    &__item {
        grid-row: 3/6;
        display: flex;
        flex-direction: column;

        &:nth-child(2n) {
            grid-row: 1/4;
            flex-direction: column-reverse;
        }

        &-chevron {
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
        
            & svg {
                //same as declared in row template
                height: 50px;
                width: auto;
                fill: inherit;
            }
        }

        &-date {
            font-size: 2rem;
            position: absolute;

            &:nth-child(2n) {
                color: $color-primary;
            }
        }

        .dotted-line {
            height: 50px;
            width: 5px;
        }

        &-content {
            display: flex;
            justify-content: center;
            border-radius: 5px;
            position: relative;
            align-items: center;

            &-desc {
                background-color: $color-white;
                width: 100%;
                margin: 1rem;
                border-radius: 5px;
                opacity: 0;
                transition: opacity 0.3s;
    
                p {
                    font-size: 1.2rem;
                    margin: .5rem;
                    font-family: 'Montserrat';
                }
            }

            &:hover {
                & > * {
                    opacity: 1;
                    background-color: red;
                }
            }
        }

        &-header {
            text-align: center;
            padding: 1rem;
            border-radius: 5px;
            white-space: nowrap;
            margin: 0rem;
            font-size: 2.5rem;
        }

    }


    @media only screen and (max-width: 600px) {
        padding: 1rem 1rem 1rem 1rem;
    }
}

